(() => {
    const injectPageTrafficListenerIntoPageScope = () => {
        const element = document.createElement('script');
        element.type = 'text/javascript';
        element.src = chrome.runtime.getURL('scripti.js');
        element.onload = function () {
            (this as HTMLScriptElement).remove();
        };
        window.document.documentElement.insertBefore(
            element,
            window.document.documentElement.firstElementChild,
        );
    };

    const checkForDOM = () => {
        if (window.document.documentElement) {
            injectPageTrafficListenerIntoPageScope();
        } else {
            checkForDOM();
        }
    };

    checkForDOM();
})();
